import React from "react";
import { Row, Col, Button, Nav, Table, Modal, Alert } from 'react-bootstrap'
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import ToggleButton from 'react-bootstrap/ToggleButton';
import { useState , useRef} from 'react';
import {Context1} from '../App';
import { useParams ,useNavigate,} from"react-router-dom";
import TabContainer from "react-bootstrap";
import Accordion from 'react-bootstrap/Accordion';
import { addItem, increace, decrease} from "../store";
import { useDispatch, useSelector } from "react-redux";
import Carousel from 'react-bootstrap/Carousel';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination } from 'swiper/modules';


import 'swiper/css';
import 'swiper/css/pagination';

const Detail = (props) => {
    let {id} = useParams();
    let {gnews} = props;
    const [radioValue, setRadioValue] = useState('1');

    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    let navigate = useNavigate();

    const radios = [
    { name: 'S', value: 'S' },
    { name: 'M', value: 'M' },
    { name: 'L', value: 'L' },
    { name: 'XL', value: 'XL' },
    { name: 'XXL', value: 'XXL' }
  ];

    let [tab, setTab] = useState(0);
    let selproduct = gnews.find((x) => x.id==id);
    
    let quantity = useSelector (state => state.quantity);
    let {pronum} = quantity;

    const dispatch = useDispatch();

    

  return (
    <div className="detailbox">
        <Row>
            
        <Col md={7}>

        <div className="firstbox">
        
        <Swiper
        spaceBetween={70}
        pagination={{
          clickable: true,
        }}
        modules={[Pagination]}
        className="sub1_slide"
      >
        <SwiperSlide><img src={process.env.PUBLIC_URL + selproduct.imgList[0]} width="100%" /></SwiperSlide>
        <SwiperSlide><img src={process.env.PUBLIC_URL +selproduct.imgList[1]} width="100%" /></SwiperSlide>
        <SwiperSlide><img src={process.env.PUBLIC_URL +selproduct.imgList[2]} width="100%" /></SwiperSlide>
        
      </Swiper>
        
        
        <div className="pdimgbox">
            <img src={process.env.PUBLIC_URL +selproduct.sideImg[0]}/>
            <img src={process.env.PUBLIC_URL +selproduct.sideImg[1]}/>
        </div>

    </div>

    <div className="sizebox">
    <img src={process.env.PUBLIC_URL +selproduct.detailinfo} className="dinfo"/>

        <h5>사이즈 가이드</h5>
        <img src={process.env.PUBLIC_URL +"/img/size1.jpg"}/>
        
    <table className="sizetable">
        <thead>
            <tr>
                <th>단위</th>
                <th>XS</th>
                <th>S</th>
                <th>M</th>
                <th>L</th>
                <th>XL</th>
            </tr>
            <tr>
                <th>가슴너비 [CHEST WIDTH]</th>
                <td>60</td>
                <td>65</td>
                <td>68</td>
                <td>72</td>
                <td>61</td>
            </tr>
            <tr>
                <th>소매길이 [SLEEVE LENGTH]</th>
                <td>58</td>
                <td>59</td>
                <td>60</td>
                <td>55</td>
                <td>62</td>
            </tr>
            <tr>
                <th>총 기장 [TOTAL LENGTH]</th>
                <td>65</td>
                <td>58</td>
                <td>70</td>
                <td>71</td>
                <td>76</td>
            </tr>
            <tr>
                <th>어깨너비 [SHOULDER WIDTH]</th>
                <td>53</td>
                <td>56</td>
                <td>61</td>
                <td>62</td>
                <td>66</td>
            </tr>
        </thead>
    </table>
   
    <div className="sizetext">
        <h5>측정 방법에 따라 1~2cm의 오차가 있을 수 있는 점 참고 부탁드립니다.</h5>
        <p>보유하고 계신 동일한 카테고리의 의류를 위 도식화와 동일한 방식으로 측정 후 <br/>
        비교하시면 구매에 많은 도움이 됩니다.</p>
        
        <h5>문의사항</h5>
        <p>상품 및 구매 관련 안내사항은 (문의하기) 또는 고객센터(02-541-0854) 로 문의주시면 <br/>
        보다 자세한 안내를 받으실 수 있습니다.</p>

        <h5>교환 및 환불</h5>
        <p>상품 수령(배송 완료) 후 7일 이 내에 교환 및 신청이 가능합니다. <br/>
        자세한 내용은 (교환 및 환불 안내) 를 통하여 확인이 가능합니다.</p>
    </div>
    </div>
            </Col>
            <Col md={5}>
                <div className="rbox">
                <h4 className='pt-5'>{selproduct.title}</h4>
                <div className="pricebox">
                <h5>가격</h5>
                <p>₩ {selproduct.price.toLocaleString()}</p>
                </div>

                <ButtonGroup>
                {
                radios.map((radio, idx) => (
                <ToggleButton
                    key={idx}
                    id={`radio-${idx}`}
                    type="radio"
                    variant={idx % 2 ? 'outline-dark' : 'outline-dark'}
                    name="radio"
                    value={radio.value}
                    checked={radioValue === radio.value}
                    onChange={(e) => setRadioValue(e.currentTarget.value)}
                >
                    {radio.name}
                </ToggleButton>
                ))}
                </ButtonGroup>
                
               
              
                <table className="order">
                    <thead>    
                    
                 
                        <tr >
                            <th>가능수량&nbsp;:&nbsp;<span>20개</span></th>
                            <th >
                                <button onClick={()=>{
                                    dispatch(decrease(1))
                                 }}>-</button>
                                <p> {pronum} </p>
                                 <button onClick={()=>{
                                    dispatch(increace(1))
                                 }}
                                >+</button> 
                            </th>
                            <th>
                            {(selproduct.price*pronum).toLocaleString()}원
                            
                            </th>          
                        </tr>  
 
                    </thead>  
                </table>

            
                <div className="buybtn">
                    <Button variant="dark"  onClick={()=>{
            dispatch(addItem({id:selproduct.id, item:selproduct.title, price1:selproduct.price*pronum, option:radioValue, imgUrl:selproduct.imgUrl, amount:pronum   

            }))
            navigate('/Cart')}}>구매하기</Button>
                    <Button variant="light" onClick={handleShow}>장바구니</Button>{' '}
                </div>
        

                <Accordion defaultActiveKey="0">
        <Accordion.Item eventKey="0">
            <Accordion.Header>배송 안내</Accordion.Header>
            <Accordion.Body>
            CJ대한통운 (1588-1255) <br/>
            배송 지역 : 전국 (일부 지역 제외)  <br/>
            배송비 : 10만원 이상 구매 시 무료 배송  <br/>
            배송 기간 : 평일 오후 2시 이전 결제 완료 된  <br/>
            주문건은 당일 출고되며 배송은 1~3일 정도 소요됩니다.
            </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="1">
            <Accordion.Header>반품안내</Accordion.Header>
            <Accordion.Body>
            교환/환불의 경우 모든 상품은 상품을 공급받으신 날로부터
            7일 이내 신청하시면 기사님께서 빠른 시일 내에 수거하실 예정입니다.
            고객님의 단순 변심으로 인한 상품의 교환/반품을 요청하시는 경우,
            택배비용은 고객님 부담이오니 이점 양해 바랍니다.
            칼하트 (마이페이지 〉 주문 내역 〉 주문서 상세 페이지 조회 〉교환/환불 신청)
            </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="2">
            <Accordion.Header>A/S 안내</Accordion.Header>
            <Accordion.Body>
            칼하트윕 한국 공식 판매처를 통하여 구입한 모든 제품은 <br/>
            A/S를 받으 실 수 있습니다.
            </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="3">
            <Accordion.Header>세탁 및 취급 시 주의사항</Accordion.Header>
            <Accordion.Body>
            제품 혼용율에 따라 세탁 및 취급 유의 사항이 변경될 수 있습니다. <br/>
            반드시 부착되어 있는 케어라벨 내용을 확인하신 후 세탁하시기 바랍니다. <br/>
            따라서 세탁시에는 반드시 세탁 및 최급 시 주의사항 페이지를 참고 부탁 드립니다.
            </Accordion.Body>
        </Accordion.Item>
        </Accordion>
                </div>
            </Col>
                    
            </Row>



      <Modal show={show} onHide={handleClose} className="cartmodal">
        <Modal.Header>
          <Modal.Title>장바구니에 상품이 담겼습니다.
        <Button variant="dark" onClick={handleClose}>계속 쇼핑하기</Button>
          <Button variant="warning" onClick={()=>{
            dispatch(addItem({id:selproduct.id, item:selproduct.title, price1:selproduct.price, imgUrl:selproduct.imgUrl, 
                option:radioValue, amount:pronum
            }))
            navigate('/Cart')}} >
            장바구니로 이동하기
          </Button>  
          </Modal.Title>
        </Modal.Header>
      </Modal>  

              
    </div>
  )
}
export default Detail
