import React, {useRef} from "react";
import {Button, Form } from 'react-bootstrap';



const Login = () => {
    const password = useRef();
    const id = useRef();

    function chkLogin(){
        var idv = id.current.value;
        var pw = password.current.value;
        var num = pw.search(/[0-9]/g);
        var eng = pw.search(/[a-z]/ig);
        var spe = pw.search(/[`~!@@#$%^&*|₩₩₩'₩";:₩/?]/gi);
        var email = /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/;
    
        if(false===email.test(idv)) {
        alert('이메일 형식이 아닙니다.');
        return false;
        }else if(pw.length < 8 || pw.length > 20){
        alert("8자리 ~ 20자리 이내로 입력해주세요.");
        return false;
        }else if(pw.search(/\s/) != -1){
        alert("비밀번호는 공백 없이 입력해주세요.");
        return false;
        }else if( (num < 0 && eng < 0) || (eng < 0 && spe < 0) || (spe < 0 && num < 0) ){
        alert("영문, 숫자, 특수문자 중 2가지 이상을 혼합하여 입력해주세요.");
        return false;
        }else { 
        alert('로그인 되었습니다.');
        return true;
        }
        
        }
    

    return (
        <>
        <div className="mainlogin">
        <div className="loginbox">
            <h2>로그인</h2>
                <Form>
            <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label>아이디</Form.Label>
                <Form.Control type="email" placeholder="이메일을 입력해주세요." ref={id}/>
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicPassword">
                <Form.Label>비밀번호</Form.Label>
                <Form.Control type="password" placeholder="비밀번호를 입력해주세요." ref={password}/>
            </Form.Group>
            <Form.Group className="mb-3" controlId="formBasicCheckbox">
                <Form.Check type="checkbox" label="아이디 저장" />

            </Form.Group>
            <Button variant="dark" type="submit" onClick={()=>{chkLogin()}}>
                로그인
            </Button>
            <Button variant="light" type="submit">
                회원가입
            </Button>
            </Form>

        </div>
        </div>
        </>
    );
};

export default Login;